const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			code: '',
			arrCode: '',
			formNode: {
				user_setting_account: '',
				user_setting_passwd: ''
			},
			codeInput: ''
		}
	},
	onLoad() {
		this.tapCode()
	},
	random(a, b) {
		return Math.round(Math.random() * (a - b) + b)
	},
	
	methods: {
		tapCode() {
			this.code = ''
			let arr = []
			let arr1 = []
			for (var i = 0; i < 26; i++) {
				arr.push(String.fromCharCode(65 + i)); //向数组中添加26位大写字母
			}
			for (var i = 0; i < 26; i++) {
				arr.push(String.fromCharCode(97 + i)); //向数组中添加26位小写字母
			}
			for (var j = 0; j <= 9; j++) {
				arr.push(j); // 向数组中添加0-9共10位数字
			}
			for (var m = 0; m < 4; m++) {
				var index=Math.floor(Math.random()*arr.length);
				console.log(index,'888')
				arr1.push(arr[index]);
				
			}
			let arr2 = arr1.join("")
			this.arrCode = arr2.toString()
		},
		getValue(e){
			let formNode = this.formNode
			let name = e.currentTarget.dataset.name
			let value = e.detail.value
			formNode[name] = value
			this.formNode = formNode
		},
		codeValue(e){
			this.codeInput = e.detail.value
			console.log(e)
		},
		next(e) {
			if(this.codeInput != this.arrCode){
				uni.showToast({
					title: '验证码不正确',
					icon: 'none'
				})
				this.tapCode()
			}
			uni.request({
				url: `${helper.requestUrl}/promote-createmycode`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: this.formNode,
				success: res => {
					// uni.hideLoading();
					res = helper.null2str(res);
					uni.showToast({
						title: res.data.tip_msg,
						icon: "none"
					});
					setTimeout( e =>{
						uni.navigateBack({
							delta: 1
						})
					},2000)
				}
			})
			// uni.navigateTo({
			// 	url: '/pages/shareCode?type=${e}'
			// })
		}
	}
}
